const tools = require('../../common/tools.js');
export default {
	data() {
		return {
			item:{}
		}
	},
	onLoad(options) {
		console.log(options.id)
		if (options.id) {
			this.id = options.id;
			this.getDetail();
		}
	},
	methods: {
		apply(){
			// tools.jumpTo("")
		},
		getDetail(){
			let that = this;
			let params = {
				pageSize: 10,
				pageNumber:1,
				id: this.id,
				
			}
			tools.request("/api/my/advisoryDetail.json", params,1,true).then(function(data) {
				that.item = data;
			})
		}
	}
}
